@charset "UTF-8";
.desktop {
  position: fixed;
  top: 30%;
  left: 40%; }

.folder_row li {
  width: 10%;
  min-width: 40px;
  margin: 1%;
  float: left; }

.folder_row:after {
  clear: both;
  display: block;
  content: ''; }

/* 아이콘 */
.folder {
  width: 100%;
  padding: 5%;
  cursor: pointer;
  border: 1px solid #fff0; }
  .folder:hover {
    background: rgba(67, 117, 217, 0.562);
    border: 1px solid #fff5; }
  .folder:active {
    background: rgba(40, 98, 214, 0.671);
    border: 1px solid #fff5; }
  .folder .text {
    color: #fff;
    font-size: 0.8em;
    text-align: center; }
  .folder img {
    width: 100%; }
